.ybtour ul{
  display: flex;
  list-style: none;
}
.ybtour ul li{
  margin-inline:5px;
}
.ybtour img{
  width:285px;
}
.ybtour p{
  color:#222;
  font-size: 16px;
  font-weight:500;
  margin:0 0 16px;
}
.ybtour p:last-of-type{
  color:#ff5c46;
  font-size: 20px;
  text-align: right;
}