.container{width:1050px; margin:0 auto;}
.kurly_h2{
  text-align: center;
}
nav ul{
  width:-moz-fit-content;
  width:fit-content;
  margin:50px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
nav ul li{
  text-align: center;
  list-style: none;
  padding:8px 10px;
  margin:10px 5px;
  border-radius: 20px;
  background-color: #e2e2e2;
}
.swiper-wrapper{
  display: flex;
  justify-content: space-around;
  align-items: first baseline;
}
.swiper-slide{
  width:250px;
}
.swiper-slide img{
  width:100%;
}
.swiper-slide p:first-of-type{
  color:#222;
  font-size: 16px;
  margin-top:0;
}
.swiper-slide p:nth-of-type(2){
  color: #b5b5b5;;
  font-size: 14px;
  font-weight:400;
  line-height: 18px;
  text-decoration: line-through;
  margin:0;
}
.swiper-slide p:last-of-type{
  color:#222;
  font-size: 16px;
  font-weight: bold;
  margin:0;
  line-height: 24px;
}
.swiper-slide p:last-of-type span{
  color:#FA622F;
  font-size: 16px;
  margin-right:5px;
}
.product{
  display: flex;
}
.product li{list-style: none; margin-right: 1rem;}
