.login_form{
  height:100vh;
}
.login_form fieldset{
  border:none;
  display: block;
  width:500px;
  height: auto;
  margin:auto;
  margin-top: 200px;
}
.login_form legend{
  font-size: 24px;
  font-weight:bold;
  text-align: center;
  margin-block:20px;
}
.login_form label{display: none;}
.login_form #id_txt, .login_form #pw_txt, .login_form .login_btn, .login_form .join_btn{
  width:calc(100% - 20px);
  height:50px;
  margin-inline:10px;
  margin-block:5px;
  
}
.login_form .search_box{
  text-align: right;
}
.login_form .search_box a{
  text-decoration: none;
  color:#333;
}
.login_form .login_btn, .login_form .join_btn{
  border:1px solid #5f0080;
  border-radius: 5px;
  font-size:18px;

}
.login_form .login_btn{
  background-color: #5f0080;
  color:#fff;
  font-size:18px;
}
.login_form .text{
  text-align: right;
}